@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    background-color: transparent;
}

body {
    background-color: #FBF6EE;
}

.hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1vw;
    align-items: center;
    margin: 1rem 0;
}

.hero .logo-header {
    display: flex;
    justify-content: center;
    gap: 1vw;
    align-items: center;
}

.hero .logo-header img {
    width: 4vw;
}

.hero .logo-header h1 {
    font-weight: 400;
    font-size: 3vw;
}

.hero .logo-header h1 span {
    color: #65B741;
}

.hero p {
    text-align: center;
    font-size: 1.7vw;
    font-weight: 300;
    margin: 0 5vw;
    color: #6c6c6c;
}

#Search {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

#Search .categories {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    background-color: #C1F2B0;
    height: 3vw;
}

#Search .categories button {
    height: 100%;
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 0.9vw;
    border: transparent;
    user-select: none;
}

#Search .categories button:hover {
    color: #FBF6EE;
    cursor: pointer;
    background-color: #65B741;
}

.clicked {
    color: #FBF6EE;
    background-color: #65B741;
}

#Search .search {
    width: 90%;
    height: 2.5vw;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
    background-color: #C1F2B0;
}

#Search .search * {
    height: 100%;
}

#Search .search .search-box {
    width: 50%;
    background-color: white;
    font-size: 1vw;
    border: 1px solid black;
}

#Search .search .select-box {
    background-color: white;
    font-size: 1vw;
    border: 1px solid black;
}

#Search .search .container {
    width: 40%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#Search .search .container * {
    width: 100%;
    height: 100%;
}

#Search .search button {
    width: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    font-size: 1.4vw;
    color: white;
    font-weight: 400;
    background-color: #65B741;
    border: 1px solid #65B741;
    cursor: pointer;
    user-select: none;
}

#Search .search button:active {
    background-color: #5da83d;
    border: 1px solid white;
}

#Search .search button .search-icon {
    color: white;
    width: 1.8vw;
    height: 1.8vw;
}

#Search #loading {
    padding: .2rem 0;
    width: 100%;
    background-color: white;
}

#Table {
    width: 90%;
    border: 0.5px solid black;
}

#Table .table-headers {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: white;
}

#Table .table-headers h4 {
    border: 0.5px solid black;
    width: 10%;
    text-align: center;
    font-weight: 400;
    font-size: 1vw;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
}

#Table .table-headers h4 img {
    width: 10%;
    height: auto;
}

#Table .table-row {
    width: 100%;
    background-color: white;
    border: 1px solid rgb(244, 244, 244);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1vw;
}

#Table .table-row span {
    width: 10%;
    border-right: 1px solid rgb(244, 244, 244);
    border-left: 1px solid rgb(244, 244, 244);
    height: 100%;
}

#Table .table-row .tag {
    text-align: center;
}

#Table .table-row:hover {
    background-color: rgb(244, 244, 244);
}

#Table .no-data {
    width: 100%;
    background-color: white;
    display: flex;
    justify-content: center;
}

.title {
    width: 60% !important;
}

.orders-in-queue {
    width: 15% !important;
}

.review-frequency {
    width: 15% !important;
}

.competition {
    width: 15% !important;
}

.last-update {
    width: 15% !important;
}